<template>
  <div>
    <mt-header :title="'DeviceName: ' + deviceName">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <mt-cell title="连接状态" :value="deviceData.online ? '在线':'离线'"></mt-cell>
    <mt-cell title="当前温度" :value="deviceData.temperature.toFixed(2)"></mt-cell>
    <mt-cell title="烟感告警" :value="deviceData.smokeAlarm?'有告警':'正常'"></mt-cell>
    <mt-cell title="开关状态" :value="deviceData.switch?'开':'关'"></mt-cell>
    <div class="page-actionsheet-wrapper">
      <p>&nbsp;</p>
      <p>&nbsp;</p>
      <p>&nbsp;</p>
      <mt-button type="primary" plain @click.native="sheetVisible = true" size="large">设置开关状态</mt-button>
    </div>
    <mt-actionsheet :actions="actions" v-model="sheetVisible"></mt-actionsheet>
  </div>
</template>

<script>
import Iot from "../lib/iot/iot.js";

var iot = new Iot();

export default {
  name: "device",
  data() {
    return {
      productKey: "",
      deviceName: "",
      deviceData: {
        online: false,
        temperature: 0,
        smokeAlarm: false,
        switch: false
      },

      switchValue: false,

      sheetVisible: false,
      actions: []
    };
  },
  beforeMount() {
    this.actions = [
      {
        name: "开",
        method: this.switchOn
      },
      {
        name: "关",
        method: this.switchOff
      }
    ];

    this.deviceName = this.$route.params.dn;
    this.productKey = this.$route.params.pk;

    setInterval(() => {
      this.refresh(this.productKey, this.deviceName);
    }, 1000);
  },
  watch: {},
  methods: {
    setSwitch(value) {
      iot
        .setSwitch(this.productKey, this.deviceName, value ? 1 : 0)
        .then(response => {
          if (response == undefined) return;

          var data = response.data;
          if (!data.Success)
            console.error("SetSwitch fail: ", data.ErrorMessage);
        })
        .catch(err => {
          console.error("onSetSwitch error: %s", err);
        });
    },
    refresh(pk, dn) {
      iot.getDeviceStatus(pk, dn).then(status => {
        this.deviceData.online = status === "ONLINE";
      });

      iot.getFireAlarmDeviceData(pk, dn).then(data => {
        this.deviceData.temperature = data.temperature;
        this.deviceData.smokeAlarm = data.smokeAlarm;
        this.deviceData.switch = data.switch;
      });
    },
    switchOn() {
      console.log("switchOn");
      this.setSwitch(true);
    },
    switchOff() {
      console.log("switchOff");
      this.setSwitch(false);
    }
  },
  components: {}
};
</script>

